/** Single **/
.single {
  .single-title {
    margin: 0;
    font-size: 1.8rem;
    line-height: 3rem;
  }

  .post-meta {
    font-size: .875rem;
    color: $global-font-secondary-color;

    span {
      display: inline-block;
    }

    .dark-theme & {
      color: $global-font-secondary-color-dark;
    }

    a {
      color: $single-link-color;

      .dark-theme & {
        color: $single-link-color-dark;
      }

      &:hover {
        color: $single-link-hover-color;

        .dark-theme & {
          color: $single-link-hover-color-dark;
        }
      }
    }

    .author {
      font-size: 1.05rem;
    }
  }

  .featured-image {
    padding-top: .6rem;

    img {
      display: block;
      max-width: 100%;
      height: auto;
      margin: 0 auto;
      overflow: hidden;
    }
  }

  @import "../_partial/_single/toc";

  .content {
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-weight: bold;
      margin: 1.2rem 0;

      .dark-theme & {
        font-weight: bolder;
      }

      & > a:first-child::before {
        content: "|";
        margin-right: .3125rem;
        color: $single-link-color;

        .dark-theme & {
          color: $single-link-color-dark;
        }
      }
    }

    h2 > a:first-child::before {
      content: "#";
    }

    p {
      font-size: 1rem;
      margin: .5rem 0;
    }

    a {
      word-break: break-all;
      color: $single-link-color;

      .dark-theme & {
        color: $single-link-color-dark;
      }
    }

    a:hover {
      color: $single-link-hover-color;

      .dark-theme &:hover {
        color: $single-link-hover-color-dark;
        font-weight: bold;
      }
    }

    ul {
      padding-left: 2rem;
      list-style-type: disc;
    }

    ruby {
      background: $code-background-color;

      rt {
        color: $global-font-secondary-color;
      }

      .dark-theme & {
        background: $code-background-color-dark;

        rt {
          color: $global-font-secondary-color-dark;
        }
      }
    }

    .table-wrapper {
      overflow-x: auto;

      &::-webkit-scrollbar {
        background-color: $table-background-color;

        .dark-theme & {
          background-color: $table-background-color-dark;
        }
      }

      > table {
        width: 100%;
        max-width: 100%;
        margin: .625rem 0;
        border-spacing: 0;
        background: $table-background-color;

        .dark-theme & {
          background: $table-background-color-dark;
        }

        thead {
          background: $table-thead-color;

          .dark-theme & {
            background-color: $table-thead-color-dark;
          }
        }

        th, td {
          padding: .3rem 1rem;
          border: 1px double $global-border-color;

          .dark-theme & {
            border: 1px double $global-border-color-dark;
          }
        }
      }
    }

    figure {
      text-align: center;

      .image-caption:not(:empty) {
        min-width: 20%;
        max-width: 80%;
        display: inline-block;
        padding: .625rem;
        margin: 0 auto;
        border-bottom: 1px solid #d9d9d9;
        font-size: .875rem;
        color: #969696;
        line-height: 1.7;
      }

      a.lightgallery:hover img {
        transform: scale(1.02);
      }
    }

    img {
      display: block;
      max-width: 96%;
      height: auto;
      margin: 0 auto;
      overflow: hidden;
      transition: all 0.2s ease-in-out;
    }

    .lazyloading {
      object-fit: none;
    }

    blockquote {
      display: block;
      border-left: .5rem solid $blockquote-color;
      background-color: rgba($blockquote-color, .2);
      padding: .25rem .75rem;
      margin: 1rem 0;

      [theme=dark] & {
        border-left-color: $blockquote-color-dark;
        background-color: rgba($blockquote-color-dark, .2);
      }
    }

    .footnotes {
      color: $global-font-secondary-color;

      .dark-theme & {
        color: $global-font-secondary-color-dark;
      }
    }

    @import "../_partial/_single/code";

    .bilibili {
      position: relative;
      width: 100%;
      height: 0;
      padding-bottom: 75%;
      margin: 3% auto;
      text-align: center;

      iframe {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
      }
    }

    hr {
      margin: 1rem 0;
      position: relative;
      border-top: 1px dashed $global-border-color;
      border-bottom: none;

      .dark-theme & {
        border-top: 1px dashed $global-border-color-dark;
      }
    }

    kbd {
      display: inline-block;
      padding: .25rem;
      background-color: $global-background-color;
      border: 1px solid $global-border-color;
      border-bottom-color: $global-border-color;
      border-radius: 3px;
      -webkit-box-shadow: inset 0 -1px 0 $global-border-color;
      box-shadow: inset 0 -1px 0 $global-border-color;
      font-size: .8rem;
      font-family: $code-font-family;
      color: $code-color;

      .dark-theme & {
        background-color: $global-background-color-dark;
        border: 1px solid $global-border-color-dark;
        border-bottom-color: $global-border-color-dark;
        -webkit-box-shadow: inset 0 -1px 0 $global-border-color-dark;
        box-shadow: inset 0 -1px 0 $global-border-color-dark;
        color: $code-color-dark;
      }
    }

    .typeit {
      .code {
        padding: .375rem;
        font-size: .875rem;
        font-family: $code-font-family;
        font-weight: bold;
        word-break: break-all;
      }
    }
  }

  @import "../_partial/_single/footer";
  @import "../_partial/_single/comment";
}
